:root {
  --color-1: #fff;
  --color-3: #ee7347;
}

header {
  margin-top: 0;
  animation: fadeIn;
  animation-duration: 1s;
}
header p {
  margin-top: -1em;
  text-transform: uppercase;
  color: var(--color-3);
  letter-spacing: 0.1em;
}
header img {
  max-width: 100%;
}

h1 {
  text-transform: none;
}

img, figure {
  margin-left: auto;
  margin-right: auto;
}

figcaption {
  margin-top: 1em;
}

figure {
  margin-top: 4em;
}

section section + section {
  margin-top: 2em;
}

#info {
  font-style: italic;
}
#info p + p {
  margin-top: 0;
}

@media (min-width: 1200px) {
  header {
    margin-top: 2em;
  }

  header img {
    max-width: 55em;
  }

  #mockups img {
    max-width: 55em;
    margin-left: -12.5em;
  }
}