:root {
    --color-1: #fff;
    --color-3: #ee7347;
}

header {
    margin-top: 0;

    p {
        margin-top: -1em;
        text-transform: uppercase;
        color: var(--color-3);
        letter-spacing: 0.1em;
    }

    img {
        max-width: 100%;
    }
    
    animation: fadeIn;
    animation-duration: 1s;
}

h1 {
    text-transform: none;
}

img, figure {
    margin-left: auto;
    margin-right: auto;
}

figcaption {
    margin-top: 1em;
}

figure {
    margin-top: 4em;
}

section {
    section + section {
        margin-top: 2em;
    }
}

#info {
    font-style: italic;
    p + p {
        margin-top: 0;
    }
}

@media(min-width: 1200px) {
    header {
        margin-top: 2em;
    }
    header img {
        max-width: 55em;
    }
    #mockups img {
        max-width: 55em;
        margin-left: -12.5em;
    }
}